@keyframes smoothAppear{from{opacity:0;transform:translateY(-5%)}to{opacity:1;transform:translateY(0)}}.info{display:flex;flex-direction:column;align-content:center;justify-content:center;height:550px;padding:0 200px;background:#f3f1f5;font-size:18pt;font-family:'Ubuntu', sans-serif;opacity:1;animation:smoothAppear 1s}.info .name{font-size:50pt;padding:10px 0}.info .school{display:inline-block;font-family:'Ubuntu', sans-serif;font-size:36pt;width:auto;margin:0;padding:0}.info .dept{font-size:28pt;padding-bottom:40px}.contents{background:#f3f1f5;padding:20px 200px;font-size:18pt;font-family:'Noto Sans KR', sans-serif;opacity:1;animation:smoothAppear 1s}

@keyframes smoothAppear{from{opacity:0;transform:translateY(-5%)}to{opacity:1;transform:translateY(0)}}.header{display:block;position:fixed;top:0;background:#716f81;height:50px;padding:5px 0px;width:100%;animation:smoothAppear 1s}.header .header-wrapper{height:45px;padding-left:40px;padding-right:40px;align-items:center;justify-content:center}.header .header-wrapper .title{display:inline-block;vertical-align:middle;height:45px;width:40%}.header .header-wrapper .title .titleBox{margin:5px 0;justify-content:center;text-align:left}.header .header-wrapper .title .titleBox .titleLink{display:block;color:white;text-decoration:none;font-size:18pt;font-family:'Gowun Batang', serif}.header .header-wrapper .link{display:inline-block;vertical-align:middle;height:45px;width:60%}.header .header-wrapper .link .linkBox{margin:5px 0;justify-content:center;text-align:right}.header .header-wrapper .link .linkBox .list{display:inline-block;margin:0 10px}.header .header-wrapper .link .linkBox .list .box{color:white;text-decoration:none;font-size:18pt;font-family:'Gowun Batang', serif}

@keyframes smoothAppear{from{opacity:0;transform:translateY(-5%)}to{opacity:1;transform:translateY(0)}}.introduce{display:flex;flex-direction:column;align-content:center;justify-content:center;height:550px;padding:60px 0;background:#f3f1f5;font-size:18pt;font-family:'Noto Sans KR', sans-serif;opacity:1;text-align:center;animation:smoothAppear 1s}.introduce .big-text{font-size:43pt;font-weight:bold;margin:0;padding:25px 0}.introduce .interest{padding:25px 0;text-align:left;font-size:20pt;font-weight:bold;display:flex;flex-direction:column;justify-content:center;align-content:center;margin:0}

