@import url('https://fonts.googleapis.com/css2?family=Gowun+Batang:wght@700&family=Noto+Sans+KR:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap');
@keyframes smoothAppear {
	from {
		opacity: 0;
		transform: translateY(-5%);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}
.info {
		display: flex;
		flex-direction: column;
		align-content: center;
		justify-content: center;
		height: 550px;
		padding: 0 200px;
		background: #f3f1f5;
		font-size: 18pt;
		font-family: 'Ubuntu', sans-serif;
		opacity: 1;
		animation: smoothAppear 1s;
		.name {
			font-size: 50pt;
			padding: 10px 0;
		}
		.school {
			display:inline-block;
			font-family: 'Ubuntu', sans-serif;
			font-size: 36pt;
			width:auto;
			margin:0;
			padding:0;
		}
		.dept{
			font-size: 28pt;
			padding-bottom:40px;
		}
	}
	.contents {
		background: #f3f1f5;
		padding: 20px 200px;
		font-size: 18pt;
		font-family: 'Noto Sans KR', sans-serif;
		opacity: 1;
		animation: smoothAppear 1s;
	}